<template>
  <div class="wiki-article-main-leftNav">
    <el-tree
      :data="data"
      :props="defaultProps"
      @node-click="handleNodeClick"
      :highlight-current="true"
      onclick="javascript:location.href='#title2'"
    ></el-tree>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "label"
      }
    };
  },
  props: ["mdList"],
  methods: {
    handleNodeClick(data) {
      let el = document.getElementById(`title${data.type_id - 1}`);
      el.scrollIntoView({
        behavior: "instant",
        block: "start",
        inline: "nearest"
      });
    }
  },
  created() {},
  watch: {
    mdList(val) {
      console.log(val);
      let data = val;
      data.forEach(element => {
        console.log(element);
        element["value"] = element["type_id"];
        element["label"] = element["subject"];
      });
      this.data = data;
    }
  }
};
</script>

<style lang="scss">
.wiki-article-main-leftNav {
  width: 250px;
  position: fixed;
  top: 164px;
  margin-top: 50px;
}
</style>