<template>
  <div class="wiki-article">
    <articleHeader></articleHeader>
    <div class="wiki-article-main">
      <h1>title</h1>
      <leftNav :mdList="mdList"></leftNav>
      <div
        v-for="(item,index) in mdList"
        :key="item.type_id"
        class="wiki-article-md look-md"
        ref="div"
        :title="item.subject"
      >
        <h2 :id="'title'+index">
          技术点：{{item.subject}}
          <a
            href="javascript:;"
            @click="toggleModify(item.type_id,index)"
            v-show="item.content"
          >修改文章</a>
        </h2>
        <el-button
          type="success"
          plain
          v-show="item.content ? false : true"
          @click="handleAdd(item.type_id,index)"
        >新增</el-button>
        <md :value="item.content" v-model="content[index].content"></md>
        <el-button
          type="primary"
          v-show="item.content"
          plain
          @click="subModify(item.type_id,index)"
        >提交修改</el-button>
        <el-button type="danger" v-show="item.content" plain @click="closeModify(index)">取消修改</el-button>

        <el-button
          type="primary"
          v-show="!item.content"
          plain
          @click="subAdd(item.type_id,index)"
        >提交新增</el-button>
        <el-button type="danger" v-show="!item.content" plain @click="closeAdd(index)">取消</el-button>
      </div>
    </div>

    <div class="both"></div>
  </div>
</template>

<script>
import articleHeader from "@/components/publicHeader";
import leftNav from '@/components/Article/leftNav' //左侧导航栏
import md from "@/components/MarkdownEditor";
import * as ajax from "@/api";

export default {
  components: {
    md,
    articleHeader,
    leftNav
  },
  data() {
    return {
      mdList: [],
      content: [],
      title: "",
    };
  },
  created() {
    ajax.getArticleDetail(JSON.parse(this.$route.query.row).id).then(resp => {
      if (resp.data.code) {
        this.mdList = resp.data.data.article_info.content;
        this.content = [...resp.data.data.article_info.content];
      }
    });
    this.title = JSON.parse(this.$route.query.row).title;
  },
  methods: {
    toggleModify(type_id, index) {
      ajax
        .authorization(JSON.parse(this.$route.query.row).id, type_id)
        .then(resp => {
          if (resp.data.code) {
            if (resp.data.data.result) {
              let el = document.querySelectorAll(".CodeMirror-code");
              el[index].innerHTML = this.mdList[index].content;
              this.$refs.div[index].classList.remove("look-md");
              let els = document.querySelectorAll("h2 a"); //点击修改tag的显示与隐藏
              els[index].style.display = "none";
            }
          } else {
            alert("您目前没有权限修改该文章内容");
          }
        });
    },
    closeModify(index) {
      if (confirm("确定取消修改吗？")) {
        this.$refs.div[index].classList.add("look-md");
        this.mdList = this.mdList;
        let els = document.querySelectorAll("h2 a"); //点击修改tag的显示与隐藏
        els[index].style.display = "inline-block";
      }
    },
    subModify(type_id, index) {
      ajax
        .modifyTechnicalPoints(
          JSON.parse(this.$route.query.row).id,
          type_id,
          this.content[index].content
        )
        .then(resp => {
          if (resp.data.code) {
            alert("提交审核中,请等待消息");
            this.$refs.div[index].classList.add("look-md");
            this.mdList = this.mdList;
            let els = document.querySelectorAll("h2 a"); //点击修改tag的显示与隐藏
            els[index].style.display = "inline-block";
          }
        });
    },
    handleAdd(id, index) {
      this.$refs.div[index].classList.remove("look-md");
      document.querySelectorAll(".el-button--success")[index].style.display =
        "none";
      this.mdList = this.mdList;
    },
    closeAdd(index) {
      if (confirm("确定取消修改吗？")) {
        this.$refs.div[index].classList.add("look-md");
        document.querySelectorAll(".el-button--success")[index].style.display =
          "block";
        this.mdList = this.mdList;
      }
    },
    subAdd(type_id, index) {
      ajax
        .modifyTechnicalPoints(
          JSON.parse(this.$route.query.row).id,
          type_id,
          this.content[index].content
        )
        .then(resp => {
          if (resp.data.code) {
            alert("提交审核中,请等待消息");
            this.$refs.div[index].classList.add("look-md");
            document.querySelectorAll(".el-button--success")[
              index
            ].style.display = "block";
          }
        });
    },
    handleNodeClick(data) {
        console.log(data);
    }
  }
};
</script>

<style lang='scss'>
.wiki-article {
  width: 100%;
  h1 {
    margin-top: 20px;
    text-align: center;
    color: #808080;
  }
  h2 {
    a {
      color: rgb(60, 148, 199);
      font-size: 15px;
      margin-left: 20px;
    }
  }
  &-main {
    margin: 0 auto;
      width: 1280px;
      margin-top: 100px;
    .wiki-article-md {
      width: 1030px;
      float: right;
      margin-top: 50px;
      border-bottom: 1px solid #ebeef5;
      .CodeMirror-scroll {
        padding-bottom: 170px;
      }
      .el-button--primary {
        float: right;
        display: inline-block;
        margin-top: 10px;
      }
      .el-button--danger {
        float: right;
        display: inline-block;
        margin-right: 20px;
        margin-top: 10px;
      }
      .el-button--success {
        margin-top: 10px;
      }
    }
  }
  .look-md {
    .tui-editor-defaultUI {
      border: none;
    }
    .te-toolbar-section {
      display: none;
    }
    .te-editor {
      display: none;
    }
    .te-md-splitter {
      border: none;
    }
    .te-mode-switch-section {
      display: none !important;
    }
    .el-button--primary {
      display: none;
    }
    .el-button--danger {
      display: none;
    }
  } //不修改时的样式
}
.wiki-article-main:after{
        content: "";
        display: block;
        height: 0;
        clear:both;
        visibility: hidden;
}
</style>

